body {
  height: 126vh;
  background-color: #383a3c;
  font-weight: 400;
  font-size: 1em;
  line-height: 1.25;
  font-family: 'Raleway', Calibri, Arial, sans-serif;
  margin: 0;
}

#spinSVG {
  width: 5em;
  height: 5em;
  min-width: 3.5em;
  min-height: 3.5em;
  background: url(https://tympanus.net/Development/AnimatedHeaderBackgrounds/img/deco.svg) no-repeat center center;
  background-size: cover;
  position: absolute;
  top: -47%;
  left: -55%;
  border-radius: 50%;
  z-index: -1;
  animation: antiClockwiseSpin 200s infinite linear;
}

.large-header {
  overflow: hidden;
}

#demo-canvas {
  transform: translateY(2em);
}

@-webkit-keyframes antiClockwiseSpin {
  0% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}

@keyframes antiClockwiseSpin {
  0% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}

header {
  display: none;
  box-shadow: 20px 6px 13px 20px rgb(33, 31, 39);
  background-image: url("https://tympanus.net/Development/AnimatedHeaderBackgrounds/img/demo-2-bg.jpg");
  background-size: cover;
  position: relative;
  height: 100%;
  border-bottom-left-radius: 79% 33%;
  border-bottom-right-radius: 79% 33%;
}

@media screen and (max-width: 320px) {
  header {
    border-bottom-left-radius: 79% 5%;
    border-bottom-right-radius: 79% 5%;
  }
}

@media screen and (max-width: 720px) and (min-width: 320px) {
  header {
    border-bottom-left-radius: 79% 8%;
    border-bottom-right-radius: 79% 8%;
  }
}

@media screen and (min-width: 720px) {
  header {
    border-bottom-left-radius: 79% 12%;
    border-bottom-right-radius: 79% 12%;
  }
}

@media screen and (min-width: 1280px) {
  header {
    border-bottom-left-radius: 79% 15%;
    border-bottom-right-radius: 79% 15%;
  }
}

.main-title {
  font-family: 'Clicker Script', cursive;
  font-weight: normal;
  font-size: 8em;
  text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2), 0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2), 0 20px 20px rgba(0, 0, 0, 0.15);
  position: absolute;
  margin: 0;
  padding: 0;
  color: #f9f1e9;
  text-align: center;
  top: 35%;
  left: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
}

.main-content {
  color: white;
  font-family: Raleway, Helvetica, sans-serif;
  font-size: 1em;
  text-align: justify;
  line-height: 1.5em;
}

#profileImage {
  float: left;
  border-radius: 50%;
  shape-outside: circle(50%);
  margin: 1em;
  box-shadow: 6px 5px 18px 8px #211f27;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

#about {
  margin: 0 20%;
}

#circleShape {
  /* four vertices (looks like rectangle) */
  shape-outside: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  transition: shape-outside 1s;

}

hr.stylized-hr {
  margin: 5em 5%;
  height: 30px;
  border-style: solid;
  border-color: #8c8b8b;
  border-width: 1px 0 0 0;
  border-radius: 20px;
}

hr.stylized-hr:before {
  display: block;
  content: "";
  height: 30px;
  margin-top: -31px;
  border-style: solid;
  border-color: #d7d6d6;
  border-width: 0 0 1px 0;
  border-radius: 20px;
}

hr.fading-hr {
  margin: 5em 5%;
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(255, 255, 255, 0.75), rgba(0, 0, 0, 0));
}

hr.fading-hr:first-of-type {
  margin-top: 2em;
}

#footer {
  margin-bottom: 0;
  padding: 0.5em;
  text-align: center;
  background-color: #1D9D73;
  word-spacing: 0.5em;
  height: 20px;
  font-family: Raleway, Helvetica, sans-serif;
  font-size: 1em;
}

#journals {
  margin: auto 20%;
}

#journals ol {
  font-style: italic;
  font-family: Georgia, Times, serif;
  font-size: 24px;
  color: #bfe1f1;
}

#journals ol li {
}

#journals ol li p {
  padding: 8px;
  font-style: normal;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 13px;
  color: #eee;
  border-left: 1px solid #999;
}

#journals ol li p em {
  display: block;
}

#footer {
  font-family: "Leckerli One", serif;
  box-shadow: inset 0 4px 9px 2px #211f27;
}

.link-buttons {
  height: 40%;
  margin: 0 20%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /*transform: skew(0deg, -10deg);*/
}

h2 {
  margin: 0 5%;
  padding-top: 1em;
  letter-spacing: 0.2em;
  font-family: "Leckerli One", serif;
  font-size: 3em;
  color: #1c7bdf;
  text-shadow: 0 1px 0 #489fdf, 0 2px 0 #1b242b, 0 3px 0 #336c96, 0 4px 0 #bfe1f1, 0 5px 0 #e0ffff, 0 6px 1px rgba(0, 0, 0, .1), 0 0 5px rgba(0, 0, 0, .1), 0 1px 3px rgba(0, 0, 0, .3), 0 3px 5px rgba(0, 0, 0, .2), 0 5px 10px rgba(0, 0, 0, .25), 0 10px 10px rgba(0, 0, 0, .2), 0 20px 20px rgba(0, 0, 0, .15);
}

.contact {
  height: 25em;
  margin: 0 20%;
  text-align: center;
  border-radius: 50%;
  background-color: lightcyan;
  box-shadow: 7px 14px 12px 7px #211f27;
}

.button {
  margin: 0 0.5em;
  font-size: 2em;
  display: inline-block;
  background: #489fdf;
  color: #fff;
  text-transform: uppercase;
  padding: 20px 25px;
  border-radius: 50%;
  box-shadow: 0 17px 10px -10px rgba(0, 0, 0, 0.4);
  cursor: pointer;
  -webkit-transition: all ease-in-out 300ms;
  transition: all ease-in-out 300ms;
}

.button:hover {
  box-shadow: 0 29px 20px -20px rgba(0, 0, 0, 0.2);
  -webkit-transform: translate(0px, -2px) scale(1.2);
  transform: translate(0px, -2px) scale(1.2);
}
